.title {
	font-family: $font-firaM;
	font-size: 32px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: normal;
	letter-spacing: normal;
	text-align: center;
	color: $color-txt1;
	margin: 70px 0 11px;
}
.desc {
	font-family: $font-fira;
	font-size: 20px;
	font-weight: normal;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.4;
	letter-spacing: normal;
	text-align: center;
	color: $color-txt1;
	margin: 0;
}
.btn {
	border-radius: 3px;
	background-color: $color-white;
	border: 1px solid rgba($color-main2, 0.3);
	font-family: $font-fira;
	font-size: 15px;
	line-height: 1.6;
	text-align: center;
	color: $color-main2;
	padding: 4px 24px;
	cursor: pointer;
	transition: background-color .3s, color .3s, box-shadow .3s, border-color .3s;
	text-decoration: none;
}
.btn-red {
	box-shadow: 0 1px 4px 0 rgba($color-red, 0.63);
	background-color: $color-red;
	border-color: $color-red;
	font-size: 16px;
	line-height: 1.5;
	color: $color-white;
	padding-top: 12px;
	padding-bottom: 12px;
}
.link {
	display: inline-block;
	font-family: $font-fira;
	font-size: 14px;
	font-weight: normal;
	font-style: normal;
	font-stretch: normal;
	line-height: normal;
	letter-spacing: normal;
	color: $color-main2;
	border-bottom: 1px dashed rgba($color-main2, 0.3);
	transition: border-bottom-color .3s;
	cursor: pointer;
	&:hover {
		border-bottom-color: rgba($color-main2, 0.6);
	}
}